import PopupWithForm from './PopupWithForm';


const DeletePlacePopup = props => {
  const handleSubmit = () => {
    props.onDeletePlace(props.cardToDelete)
  };
  
  return(
    <PopupWithForm
      onSubmit={handleSubmit}
      validate={false}
      title="?"
      name="delete-place"
      submitText=""
      {...props}
    />
  );
};

export default DeletePlacePopup;